import React, { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import {
  AppBar,
  Toolbar,
  Button,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemText,
  Box,
  useTheme,
  Typography,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import SearchIcon from "@mui/icons-material/Search";
import { ThemeProvider } from "@mui/material/styles";

const Navbar = () => {
  const location = useLocation();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = useState(false);

  const navItems = [
    { label: " ", path: "/" },
    { label: " ", path: "/speakers" },
    { label: "", path: "/register" },
    { label: " ", path: "/invitation" },
    { label: "   ", path: "/about" },
    { label: " ", path: "/publication" },
  ];

  const handleDrawerToggle = () => setMobileOpen(!mobileOpen);

  return (
    <ThemeProvider theme={theme}>
      <Box sx={{ display: "flex", justifyContent: "flex-end", width: "100%" }}>
        <AppBar
          position="static"
          sx={{
            backgroundColor: theme.palette.success.main,
            height: "8.5rem",
            width: { xs: "100%", sm:"70%", md: "65%" },
            borderBottomLeftRadius: { xs: "7.75rem", md: "7.75rem" }, //  curve in mobile too
            boxShadow: "none",
            display: "flex",
            justifyContent: "center",
          }}
        >
          <Toolbar
            sx={{
              display: "flex",
              justifyContent: { xs: "space-between", md: "center" },
              alignItems: "center",
              height: "100%",
              px: 3,
              pt: 4,
              pb: 2,
              width: "100%",
            }}
          >
            {/* Mobile Menu Button */}
            <IconButton
              color="inherit"
              onClick={handleDrawerToggle}
              sx={{ display: { md: "none" } }}
            >
              <MenuIcon sx={{ color: "#fff" }} />
            </IconButton>

            {/* Desktop Menu */}
            <Box
              sx={{
                display: { xs: "none", md: "flex" },
                alignItems: "center",
                justifyContent: "space-evenly", // Equal spacing
                width: "100%", // Take full width
                mt: 3,
              }}
            >
              {navItems.map((item) => (
                <Button
                  key={item.path}
                  component={Link}
                  to={item.path}
                  color="inherit"
                  sx={{
                    fontSize: "1.375rem",
                    color: location.pathname === item.path ? "#5F695C" : "#fff",
                    fontWeight:
                      location.pathname === item.path ? "bold" : "normal",
                  }}
                >
                  {item.label}
                </Button>
              ))}
              <IconButton color="inherit">
                <SearchIcon sx={{ fontSize: 26, color: "#fff" }} />
              </IconButton>
            </Box>
          </Toolbar>
        </AppBar>

        {/* Drawer for Mobile */}
        <Drawer
          anchor="left"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          PaperProps={{
            sx: {
              backgroundColor: theme.palette.success.main, //  same green background
              color: "#fff", //  white text
              width: "65%",
              borderTopRightRadius: "2rem", // optional curve
              borderBottomRightRadius: "2rem",
            },
          }}
        >
          <Box sx={{ height: "100%", pt: 3 }}>
            <List>
              {navItems.map((item) => (
                <ListItem
                  button
                  key={item.path}
                  component={Link}
                  to={item.path}
                  onClick={handleDrawerToggle}
                  sx={{
                    color: "#fff",
                    "&:hover": {
                      backgroundColor: "rgba(255,255,255,0.15)",
                    },
                  }}
                >
                  <ListItemText
                    primary={
                      <Typography
                        sx={{
                          fontSize: "1.2rem",
                          fontWeight:
                            location.pathname === item.path ? "bold" : "normal",
                        }}
                      >
                        {item.label}
                      </Typography>
                    }
                  />
                </ListItem>
              ))}
            </List>
          </Box>
        </Drawer>
      </Box>
    </ThemeProvider>
  );
};

export default Navbar;
